/* Import Style */

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: auto;
  overflow: auto;
}

.wrapper {
  position: relative;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  border-top: 1.5px solid;
  border-image: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  border-image-slice: 1;
}

.strips {
  position: fixed;
  width: 100%;
  height: auto;
}

.strips__one,
.strips__two,
.strips__three,
.strips__four,
.strips__five,
.strips__six {
  position: absolute;
  width: 1px;
  height: 100vh;
  /* background: rgba(65, 65, 65, 0.05); */
  background: rgb(65 65 65 / 5%);
}

.strips__one {
  left: 8%;
}
.strips__two {
  left: 24.8%;
}
.strips__three {
  left: 41.6%;
}
.strips__four {
  left: 58.4%;
}
.strips__five {
  left: 75.2%;
}
.strips__six {
  left: 92%;
}

.strips__one:after,
.strips__two:after,
.strips__three:after,
.strips__four:after,
.strips__five:after,
.strips__six:after {
  content: "";
  position: absolute;
  left: 0;
  top: 100%;
  width: 1px;
  height: 20px;
  background: rgba(0, 0, 0, 0.3);
  -webkit-animation: animStrips 4s ease-in-out infinite;
  animation: animStrips 4s ease-in-out infinite;
}

.strips__one:after {
  -webkit-animation-delay: 1.2s;
          animation-delay: 1.2s;
}
.strips__two:after {
  -webkit-animation-delay: 400ms;
          animation-delay: 400ms;
}
.strips__three:after {
  -webkit-animation-delay: 1.6s;
          animation-delay: 1.6s;
}
.strips__four:after {
  -webkit-animation-delay: 0;
          animation-delay: 0;
}
.strips__five:after {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.strips__six:after {
  -webkit-animation-delay: 800ms;
          animation-delay: 800ms;
}

@keyframes animStrips {
  0% {
    top: 100%;
  }
  100% {
    top: -20px;
  }
}

@-webkit-keyframes animStrips {
  0% {
    top: 100%;
  }
  100% {
    top: -20px;
  }
}

