}
.cta__btn:hover .cta__box {
  width: calc(100% - 6px);
  background: #3680f7;
  /* border: 2px solid transparent; */
}
.cta__btn .cta__box svg {
  position: absolute;
  left: 5px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 18px;
