/* CURSOR */
.cursor {
  position: absolute;
  background: #ffffff;
  width: 8px;
  height: 8px;
  border-radius: 100%;
  z-index: 9999;
  transition: 0.2s cubic-bezier(0.75, -1.27, 0.3, 2.33) opacity,
    0.5s cubic-bezier(0.75, -1.27, 0.3, 2.33) -webkit-transform;
  transition: 0.5s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform,
    0.2s cubic-bezier(0.75, -1.27, 0.3, 2.33) opacity;
  transition: 0.5s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform,
    0.2s cubic-bezier(0.75, -1.27, 0.3, 2.33) opacity,
    0.5s cubic-bezier(0.75, -1.27, 0.3, 2.33) -webkit-transform;
  -webkit-user-select: none;
          user-select: none;
  pointer-events: none;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

.cursor::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background-image: url(data:image/png;base64,);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 100%;
  opacity: 0;
}

.cursor.active {
  opacity: 1;
  -webkit-transform: scale(12);
          transform: scale(12);
}

.cursor.active::before {
  opacity: 1;
}

.cursor-follower {
  position: absolute;
  background: rgba(0, 0, 0, 0.1);
  width: 40px;
  height: 40px;
  border-radius: 100%;
  z-index: 9999;
  transition: 0.4s cubic-bezier(0.75, -1.27, 0.3, 2.33) opacity,
    0.6s cubic-bezier(0.75, -1.27, 0.3, 2.33) -webkit-transform;
  transition: 0.6s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform,
    0.4s cubic-bezier(0.75, -1.27, 0.3, 2.33) opacity;
  transition: 0.6s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform,
    0.4s cubic-bezier(0.75, -1.27, 0.3, 2.33) opacity,
    0.6s cubic-bezier(0.75, -1.27, 0.3, 2.33) -webkit-transform;
  -webkit-user-select: none;
          user-select: none;
  pointer-events: none;
  -webkit-transform: translate(4px, 4px);
          transform: translate(4px, 4px);
}

.cursor-follower.active {
  opacity: 0.3;
  -webkit-transform: scale(0);
          transform: scale(0);
}
/* CURSOR */

/* LOADER */
.loaderwrapper {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 9999;
  background: url(/static/media/wallpaper_bg.df340874.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-animation: animatedBackground 1000s linear infinite;
          animation: animatedBackground 1000s linear infinite;
}
.loaderwrapper:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -20;
  opacity: 0.1;
  height: 100%;
  min-height: 100%;
  max-height: 100%;
  width: 400%;
  background-image: url(data:image/svg+xml;base64,);
  background-position: center bottom;
  -webkit-animation: animatedBk 700s linear infinite;
  animation: animatedBk 700s linear infinite;
}
.loaderwrapper::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  min-height: 100%;
  max-height: 100%;
  width: 400%;
  opacity: 0.1;
  background-image: url(data:image/svg+xml;base64,);

  background-position: center bottom;
  z-index: -20;
  -webkit-animation: animatedBackground 400s linear infinite;
  animation: animatedBackground 400s linear infinite;
}
@-webkit-keyframes animatedBackground {
  0% {
    background-position: 0 100%;
  }
  50% {
    background-position: 100% 100%;
  }
  100% {
    background-position: 0% 100%;
  }
}
@keyframes animatedBackground {
  0% {
    background-position: 0 100%;
  }
  50% {
    background-position: 100% 100%;
  }
  100% {
    background-position: 0% 100%;
  }
}

@-webkit-keyframes animatedBk {
  0% {
    background-position: 100% 100%;
  }
  50% {
    background-position: 0% 100%;
  }
  100% {
    background-position: 100% 100%;
  }
}
@keyframes animatedBk {
  0% {
    background-position: 100% 100%;
  }
  50% {
    background-position: 0% 100%;
  }
  100% {
    background-position: 100% 100%;
  }
}
.loader__svg {
  position: absolute;
  top: 47%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  overflow: hidden;
  z-index: 999;
}
.icon {
  width: 120px;
  opacity: 0;
}
.icon path {
  stroke-width: 3px;
  stroke-dashoffset: -1840;
  stroke-dasharray: 1840;
}
.icon path.icon__active {
  -webkit-animation-name: icon;
          animation-name: icon;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes icon {
  50% {
    stroke-dashoffset: 0;
    fill: transparent;
  }
  75% {
    fill: transparent;
    stroke-width: 3px;
  }
  100% {
    stroke-dashoffset: 0;
    fill: #f0ad47;
    stroke-width: 0px;
  }
}
@keyframes icon {
  50% {
    stroke-dashoffset: 0;
    fill: transparent;
  }
  75% {
    fill: transparent;
    stroke-width: 3px;
  }
  100% {
    stroke-dashoffset: 0;
    fill: #f0ad47;
    stroke-width: 0px;
  }
}
.loader__tagline {
  text-transform: uppercase;
  margin-top: 25px;
