/* CIRCULAR TEXT ANIMATION */
.circular__text__container {
  position: fixed;
  bottom: 30px;
  right: calc(6.5% - 47.5px);
  z-index: 999;
}
.circular__text {
  position: relative;
  text-transform: uppercase;
  font-size: 10px;
  font-family: Gilroy_extrabold;
  letter-spacing: 1px;
  transition: 1s ease-out;
  color: #f0ad47;
  -webkit-animation: animRotated 8s infinite linear;
          animation: animRotated 8s infinite linear;
  background: #fff;
  display: none;
}
.brand__icon.active {
  opacity: 0;
}
@-webkit-keyframes animRotated {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes animRotated {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.center__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #f0ad47;
  font-size: 20px;
}

