/* Link Animation css */
.link-animation {
  --h: 1.2em; /* the height */
  linea-height: var(--h);
  color: #0000;
  overflow: hidden;
  background: linear-gradient(#3680f7 0 0) bottom/100% 0 no-repeat;
  background: linear-gradient(#3680f7 0 0) bottom/100% var(--_d, 0) no-repeat;
  transition: 0.4s;
  padding: 0 5px;
}
.link-animation:hover {
  --_d: 100%;
  --_t: 0;
  --_c: rgba(0, 0, 0, 0);
}

.link-animation:hover .animation-hover {
  color: #fff;
}

.link-animation:hover .cloud-icon {
  color: #fff;
}
