/* Image Overlay Effect */
.main-title {
  color: #2d2d2d;
  text-align: center;
  text-transform: capitalize;
  padding: 0.7em 0;
}
.content {
  position: relative;
  width: 100%;
  max-width: 400px;
  margin: auto;
  overflow: hidden;
  display: flex;
}

.content .content-overlay {
  background: rgba(0, 0, 0, 0.7);
  /* background: #3680f763; */
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  -webkit-transition: all 0.4s ease-in-out 0s;
  -moz-transition: all 0.4s ease-in-out 0s;
  transition: all 0.4s ease-in-out 0s;
}

.content:hover .content-overlay {
  opacity: 1;
}

.content-image {
  width: 100%;
}

.content-details {
  position: absolute;
  text-align: left;
  padding-left: 1em;
  padding-right: 1em;
  width: 100%;
  top: 50%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: all 0.3s ease-in-out 0s;
  -moz-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

.content:hover .content-details {
  top: 55%;
  left: 50%;
  opacity: 1;
}

.fadeIn-left {
  left: 20%;
}

.content-details h3 {
  font-family: "Poppins", sans-serif !important;
  font-size: 16px;
  font-weight: 500;
  padding: 0 1.5rem;
  letter-spacing: 1px;
  text-transform: capitalize;
  margin-bottom: 1.5rem;
  color: #fff;
  line-height: 1.1;
}

.content-details p {
  font-family: "Poppins", sans-serif !important;
  font-size: 14px;
