#footer {
  position: relative;
  width: 100%;
  height: auto;
  /* background: url(../../assets/images/footer_bg.jpg) no-repeat center; */
  background-color: #eaecef;
  background-size: cover;
  padding: 30px 0;

  padding-top: 6rem;
  padding-bottom: 1rem;
}

.footer_container {
  position: relative;
  width: 100%;
}

.footer__brand_icon {
  position: relative;
}
.footer__brand_icon img {
  width: 50px;
}
.footer__brand_icon p {
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 400;
  margin-left: 5px;
  margin-top: -5px;
}

.footer__sm__list {
  position: relative;
}
.footer__sm__list .footer__sm__menu {
  list-style: none;
  padding: 0;
  margin: 15px 0 0 0;
}
.footer__sm__list .footer__sm__menu .footer_sm_item {
  display: inline-block;
  margin: 0 10px;
}
.footer__sm__list .footer__sm__menu .footer_sm_item a {
  text-decoration: none;
  color: #fff;
  font-size: 25px;
  transition: all 0.5s ease-out;
}
.footer__sm__list .footer__sm__menu .footer_sm_item a:hover {
  color: #3680f7;
}

.footer-social-navlink-sec {
  border-bottom: 1.5px solid rgb(128 128 128 / 31%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 10px;
}

.footer-link-sec ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.footer-link-sec ul li {
  margin: 0 15px;
}

.footer-link-item {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  color: #000;
  text-decoration: none;
  transition: all 0.8s ease-in-out;
}

.footer-link-item:hover {
  margin-top: -3px;
  color: #3680f7;
}

.footer-laquest-bottom-sec {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 5px;
}

.footer-laquest-bottom-sec p {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  color: #000;
  font-size: 14px;
}

.bottom-footer-link {
  font-weight: 400;
  font-size: 14px;
}

.footer-social-navlink-sec .contact__social__container ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}

.footer-social-navlink-sec .google__partner__logo a img {
  width: 100px;
}

@media screen and (max-width: 425px) {
  .footer-social-navlink-sec .footer-link-sec ul li {
    margin: 0 10px;
  }

  .footer-social-navlink-sec {
    flex-wrap: wrap;
    justify-content: center;
  }

  .google-cloud-social-link {
    display: flex;
    align-items: center;
    padding-bottom: 10px;
  }

  .contact__social__container ul li {
    margin: 0 20px;
  }
}
